/* IMAGE */
.event-img-wrapper {
  position: relative;
  height: 160px;
}

.event-img-wrapper img {
  object-fit: cover;
}

/* STATUS */
.event-status {
  position: absolute;
  top: 12px;
  right: 12px;
  border-radius: 3px;
  padding: 4px 12px;
  font-size: 12px;
  font-weight: 600;
  text-transform: capitalize;
}

/* STATUS COLORS */
.event-status.approved {
  background-color: #e6f4ea;
  color: #137333;
}
.event-status.rejected {
  background-color: #fdecea;
  color: #b3261e;
}
.event-status.pending {
  background-color: #fff4e5;
  color: #b26a00;
}
.event-status.private {
  background-color: #eef1f6;
  color: #344054;
}
.event-status.draft {
  background-color: #f2f2f2;
  color: #6b7280;
}

/* TITLE ROW */
.event-title-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* MENU WRAPPER (IMPORTANT FIX) */
.event-menu-wrapper {
  position: relative;
}

/* THREE DOT */
.event-menu {
  font-size: 18px;
  cursor: pointer;
  padding: 2px 6px;
  border-radius: 6px;
}

.event-menu:hover {
  background-color: #f1f1f1;
}

/* DROPDOWN */
.event-menu-dropdown {
  position: absolute;
  top: -18px;
  right: 15px;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.12);
  z-index: 20;
  overflow: hidden;
  min-width: 120px;
}

.dropdown-item {
  padding: 10px 14px;
  font-size: 14px;
  cursor: pointer;
  background: none;
  border: none;
  width: 100%;
  text-align: left;
}

.dropdown-item:hover {
  background-color: #f5f5f5;
}

.dropdown-item.delete {
  color: #dc3545;
}

/* EVENT META */
.event-meta {
  font-size: 13px;
  color: #6b7280;
  display: flex;
  flex-direction: column;
  gap: 4px;
  margin-top: 8px;
}
/* STATUS BADGE BASE */
.event-list-status {
  display: inline-block;
  padding: 6px 14px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  text-transform: capitalize;
  line-height: 1;
  width: fit-content;
}

/* APPROVED */
.event-list-status.approved {
  background-color: #e6f4ea;
  color: #137333;
  border: 1px solid #b7e1cd;
}

/* PENDING */
.event-list-status.pending {
  background-color: #fff4e5;
  color: #b26a00;
  border: 1px solid #ffd8a8;
}

/* REJECTED */
.event-list-status.rejected {
  background-color: #fdecea;
  color: #b3261e;
  border: 1px solid #f5c2c7;
}

/* DRAFT */
.event-list-status.draft {
  background-color: #f2f2f2;
  color: #6b7280;
  border: 1px solid #e5e7eb;
}

/* PRIVATE */
.event-list-status.private {
  background-color: #eef1f6;
  color: #344054;
  border: 1px solid #d0d5dd;
}
.event-card{
  cursor: pointer;
}
